import Home from './components/Home.vue';
import Skill from './components/Skill.vue';
import Hobby from './components/Hobby.vue';

const routes = [
    { path: '/', component: Home },
    { path: '/skill', component: Skill },
    { path: '/hobby', component: Hobby },
];

export default routes;



